class ThisComponent extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      logoIndex: 0,
      logosLength: 6,
      logoText: '',
    }
  }

  updateLogoIndex = (newIndex) => {
    if (typeof index === 'undefined') {
      let length = this.state.logosLength
      newIndex = this.state.logoIndex + 1
      if (newIndex >= length) {
        newIndex = newIndex - length
      }
    }
    this.setState({
      logoIndex: newIndex,
    })
  }

  updateLogoText = () => {
    let pathlist = this.props.location.pathname.split('/')
    this.setState({
      logoText: pathlist[1],
    })
  }

  componentDidMount() {
    this.updateLogoText()
  }

  componentDidUpdate(prevProps) {
    if (this.props.location.pathname !== prevProps.location.pathname) {
      this.updateLogoText()
    }
  }

  render() {
    let Logo = (
      <Link to="/" className="Link" key="fnew">
        <span className="vmiddle color-accent" style={{ marginLeft: '1px' }}>name</span>
        <span className="vmiddle color-white" style={{ marginLeft: '0.1rem' }}>brain</span>
        <span className="vmiddle color-accent" style={{ marginLeft: '0.1rem' }}>.ai</span>
      </Link>
    )

    return (
      <StyledHead className="StyledHead">
        <StyledHeader className="StyledHeader content">
          <StyledLogo
            className="StyledLogo"
            onClick={() => {
              this.updateLogoIndex()
            }}
          >
            {Logo}
          </StyledLogo>
          <StyledRightburger className="StyledRightburger">
            <Dropdown className="Dropdown" overlay={<AccountMenu {...this.props} />}>
              <div>
                <i>...</i>
              </div>
            </Dropdown>
          </StyledRightburger>
        </StyledHeader>
      </StyledHead>
    )
  }
}

const mapStateToProps = function(state) {
  return {
    word_input: state.word.input,
  }
}
export default connect(mapStateToProps)(ThisComponent)

