import React from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Link } from 'gatsby'
import { WordPossStyled } from './WordPoss.styled'
import PosWord from 'src/components/EditWord/PosWord'
import api_actions from '../../redux/actions/api'
import InputGroup from '../EditWord/InputGroup'

class WordPoss extends React.Component {
  render() {
    let { word_chunks, word_tld, show_poss, api_actions } = this.props
    if (!word_chunks.length) {
      return null
    }
    let chunks = word_chunks.flat()
    if (word_tld) {
      chunks.push(word_tld)
    }
    if (!chunks.length) {
      return null
    }
    /*
     * Render:
     */
    return (
      <WordPossStyled className={'WordPossStyled ' + (show_poss ? ' show' : ' hide')}>
				<section className={'columns'}>
					{chunks.map(row => {
            if (!row || !row.poss) {
              return null
            }
            let poss = row.poss
            let poss_keys = Object.keys(poss)
            return (
              <div key={row.key} className="column">
                {/*
                 * word key:
                 */}
                <b><Link to={`/edit_word?word=${row.key}`} target="_blank">{row.key}</Link></b>:
                {/*
                 * view/edit word:
                 */}
                <div className="">
                {poss_keys.map(poss_key => {
                  return (
                    <section key={poss_key} className={'columns'}>
											<div className="column">
                        {/*
                         * pos key:
                         */}
                        <h5 className="color-dark nn">{poss_key}:</h5>
                        {/*
                         * add synonyms to pos:
                         */}
                        {!!(row.pos_dict[poss_key]) &&
                        <InputGroup className="pos_inputgroup" word={row.key} pos={poss_key} api_actions={api_actions} />
                        }
                        {/*
                         * view/edit pos:
                         */}
                        <div className="words">
                        {(poss[poss_key] && poss[poss_key].map)
                          ? (
                            poss[poss_key].map(w => (
                              !poss_key.includes('domains')
                                ? (
                                  <span key={w}>
                                    <PosWord
                                      row={row}
                                      word={w}
                                      info={undefined}
                                      api_actions={api_actions}
                                    />
                                  </span>
                                )
                                : (
                                  <span key={w}>
                                    <Link target="_blank" to={'/edit_domain?tld=' + w}>{w}</Link>, &thinsp;
                                  </span>
                                )
                            ))
                          )
                          : (
                            <span className="object">{JSON.stringify(poss[poss_key], null, ' ')}</span>
                          )
                        }
                        </div>
											</div>
										</section>
                  )
                })}
                </div>
							</div>
            )
          })}
				</section>
			</WordPossStyled>
    )
  }
}

const mapDispatchToProps = dispatch => {
  return {
    api_actions: bindActionCreators(api_actions, dispatch),
  }
}


const mapStateToProps = function(state) {
  return {
    show_poss: state.ui.show_poss,
    word_chunks: state.word.chunks,
    word_tld: state.word.tld,
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(WordPoss)
