const API_HOST = process.env.REACT_APP_API_HOST

export function domain_suggestions(options = {}) {
  return async () => {
    if (!options || !options.phrase) {
      return
    }
    window.isLoading('data')
    try {
      let { phrase, ...postData } = options
      let results = await apiGET(API_HOST + '/v1/phrase/' + phrase, postData)
      // const results = await axios({
      //   'method': 'GET',
      //   'url': API_HOST + '/v1/phrase/' + phrase,
      //   // 'headers': API_HEADERS,
      //   'params': postData,
      //   'crossdomain': true,
      //   'mode': 'cors',
      // })
      // success
      window.doneLoading('data')
      return results
    } catch (e) {
      // fail
      window.doneLoading('data')
    }
  }
}