import styled from 'styled-components'

let isRetina = function() {
  // return boolean:
  return typeof window !== 'undefined'
    ? typeof window.matchMedia !== 'undefined' ? window.matchMedia('(-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2), (min-resolution: 192dpi)').matches : false
    : false
}

