// import { RX__tld_add } from 'src/redux/actions/domain'
import { domain_suggestions } from 'src/redux/actions/api/v1'
import { message } from 'antd'

const SECURE = process.env.GATSBY_ACTIVE_ENV !== 'development'

/*
 * input
 */
export function RX__word_input_word(str = '') {
  return {
    type: 'RX__word_input_word',
    listType: str,
  }
}

export function RX__word_input_tld(str = '') {
  return {
    type: 'RX__tld_input_tld',
    listType: str,
  }
}

/*
 * words
 */
export function RX__word_phrase(phrase = undefined, tld = '', options = { captcha_response: '' }) {
  return async (dispatch, getState) => {
    // default word
    let { word } = getState()
    if (phrase === undefined) {
      phrase = word.input_str
    }

    // captcha
    if (SECURE && !options.captcha_response) {
      message.error(`sorry. Something went wrong. Please try again`)
      return false
    }

    // API options
    options = {
      ...options, ...{
        phrase: phrase,
        tld: tld,
        _phrase_chunks: true,
        _phrase_tld: true,
        _domains: true,
        _tlds: true,
        _phrases: true,
        _wip_phrases: true,
        _wip_domains: true,
      },
    }

    // REQUEST
    let data = await dispatch(domain_suggestions(options)) || {}

    // REQUEST done
    dispatch({
      type: 'RX__tld_user',
      data: data.tld || tld,
    })
    dispatch({
      type: 'RX__word_input_tld',
      data: data.tld || tld,
    })
    dispatch({
      type: 'RX__word_input_str',
      data: data.phrase || phrase,
    })
    dispatch({
      type: 'RX__word_chunks',
      data: data.phrase_chunks || [],
    })
    dispatch({
      type: 'RX__word_tld_chunk',
      data: data.phrase_tld || {},
    })
    dispatch({
      type: 'RX__domain_suggestions',
      results: {
        domains: data.domains || [],
        wip_domains: data.wip_domains || [],
        phrases: data.phrases || [],
        wip_phrases: data.wip_phrases || [],
      },
    })
    dispatch({
      type: 'RX__tlds_checked',
      data: data.tlds || [],
    })
  }
}