export function data_word_add_poswords(key, pos, poswords) {
  return async (dispatch) => {
    // prep
    let poslist = poswords.split(',').map(w => w.trim())
    if (poslist && poslist.length) {
      // save
      let success = await apiPUT(API_HOST + '/api/data/word_add_poswords', { key, pos, poswords: poslist })
      if (success) {
        // ui alert
        message.success(`added poswords="${poswords}" to pos="${pos}"`)
        // update data for each word
        dispatch(RX__word_phrase())
        return true
      }
    }
    message.error(`error saving key=${key}, pos=${pos}, poswords=${poswords}`)
    return false
  }
}

export function data_word_remove_words(key, wordsString) {
  return async (dispatch) => {
    // prep
    let wordsList = wordsString.split(',').map(w => w.trim())
    if (wordsList && wordsList.length) {
      // save
      let success = await apiPUT(API_HOST + '/api/data/word_remove_words', { key, words: wordsList })
      if (success) {
        // ui alert
        message.success(`removed poswords="${wordsString}" from key="${key}"`)
        // update data for each word
        dispatch(RX__word_phrase())
        return true
      }
    }
    message.error(`error saving key=${key}, words=${wordsString}`)
    return false
  }
}

